<template>
  <div class="template-container">
    <div class="template-item-wrap">
      <!-- 时间选择器 begin -->
      <div class="temp-item-inlineblock">
        <el-date-picker @change="pickerDateChange" v-model="pickerDate" type="daterange" align="left" unlink-panels range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" :picker-options="pickerOptions2">
        </el-date-picker>
      </div>
      <!-- 时间选择器 end  -->
      <div class="temp-item-inlineblock">
        <el-select v-model="adgroupsParams.system_status" placeholder="请选择状态">
          <el-option v-for="item in ad_system_status" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-select v-model="adgroupsParams.product_type" placeholder="标的物类型">
          <el-option v-for="item in productTypeList" :key="item.value" :label="item.name" :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="temp-item-inlineblock">
        <el-input placeholder="广告组名" v-model="adgroupsParams.adgroup_name"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-input placeholder="广告组ID" v-model="adgroupsParams.adgroup_id"></el-input>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="searchList">查询</el-button>
      </div>
      <div class="temp-item-inlineblock">
        <el-button type="primary" @click="exportExcel">导出为excel</el-button>
      </div>
      <div class="temp-item-inlineblock temp-item-right">
        <el-button type="primary" @click="newAdGroup">新建广告组</el-button>
      </div>

      <div class="temp-item-block">
        <el-button-group>
          <el-button type="primary" @click="handleBatchRemove">删除</el-button>
          <el-button type="primary" @click="handleBatchChangeDateRange">修改日期</el-button>
          <el-button type="primary" @click="handleBatchChangeDateRange">修改时间</el-button>
          <el-button type="primary" @click="handleBatchChangeBidAmount">修改出价</el-button>
        </el-button-group>
        <el-dropdown split-button type="primary" @click="submitSystemStatus" @command="handleCommand">
          修改状态
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item :command="key" :key="key" v-for="(val, key) in configured_status_list">{{ val }}</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-popover ref="popover" placement="bottom-start" visible-arrow="false" width="660" v-model="visible">
          
          <div class="nav-pop">
          <div class="datepop">
            <div class="selectarea">
            <form onsubmit="return false;" action="return false;">
              <ul class="select-classify clearfix" id="select-classify">
                <li class="classify-item require" v-for="(checkItem,checkKey) in checkBox" :disabled="checkItem.disabled === true">
                  <div class="tit">
                  <strong><el-checkbox v-model="checkBoxAll[checkKey]" :disabled="checkItem.disabled === true" @change="handleCheckAllChange(checkKey)">{{checkItem.name}}</el-checkbox></strong>
                  </div>
                  <ul class="checkitem clearfix">
                <el-checkbox-group v-model="checkList[checkKey]">
                    <li class="checkitem-li" v-for="(subItem,subKey) in checkItem.sub">
                      <el-checkbox  :label="subKey" :disabled="checkItem.disabled === true">{{subItem}}</el-checkbox>
                    </li>
                </el-checkbox-group>
                  </ul>
                </li>
              </ul>
            </form>
            </div>
          </div>
          </div>
          <div style="text-align: right; margin-top: 10px">
            <el-button size="mini" type="text" @click="checkBoxCancel">取消</el-button>
            <el-button type="primary" size="mini" @click="checkBoxSubmit">确定</el-button>
          </div>
        </el-popover>
        <el-button v-popover:popover>自定义列</el-button>
      </div>
    </div>
    <div class="table-wrap" v-loading="loading">
      <el-table show-overflow-tooltip :summary-method="getSummaries" show-summary ref="multipleTable" highlight-current-row @sort-change="changeSort"
      @expand-change="handleCurrentTable" @selection-change="handleSelectionChange" :data="listData" style="width: 95%">

      <el-table-column type="expand">
        <template slot-scope="scope">
          <div v-loading="scope.row.loading" style="overflow:hidden;" element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0.8)">
          <div>
            <template>
              <el-table :data="adsList[scope.row.adgroup_id]" border style="width: 100%;">
                  <el-table-column prop="ad_name" label="广告名称" >
                  </el-table-column>
                  <el-table-column v-for="(item, index) in checkBox[2]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)">
                  </el-table-column>
                  <el-table-column v-for="(item, index) in checkBox[3]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)">
                  </el-table-column>
                  <el-table-column v-for="(item, index) in checkBox[4]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)">
                  </el-table-column>
              </el-table>
            </template>
          </div>
            <div style="margin-top:10px;">
              <h3>广告组详细</h3>
              <div class="block ggao-display" style="width:28%;float:left;">
                <el-carousel type="card" height="200px">
                <!-- <el-carousel trigger="click" height="200px"> -->
                  <el-carousel-item v-for="(item, index) in imagesList[scope.row.adgroup_id]" :key="index">
                    <p class="place"><strong>广告名称：</strong> {{ item.ad_name }}</p>
                    <!-- <p><strong>创意名称：</strong> <a :href="item.adcreative.destination_url" :title="item.adcreative.adcreative_name" target="_blank" class="_title">{{item.adcreative.adcreative_name}}</a> </p>  -->
                      <div class=""> 
                      <div class="show-tuwen"> 
                        <a :href="item.destination_url" :title="item.adcreative_name" target="_blank"> <img :src="item.image_url" style="max-height: 200px; height: 137.75px" /> </a> 
                      </div>
                      <p>{{ item.title }}</p> 
                      </div>
                  </el-carousel-item>
                </el-carousel>
              </div>
              <div class="edit-area"> 
                <p class="info"><span class="item">广告组名</span><span>{{ scope.row.adgroup_name }}</span></p>
                <p class="info"><span class="item">广告组ID</span><span>{{ scope.row.adgroup_id }}</span></p>
                <p class="info"><span class="item">投放日期</span><span>{{ scope.row.begin_date }}~{{ scope.row.end_date }}</span></p>
                <p class="info"><span class="item">投放时间</span>
                  <span v-if="scope.row.time_series_range.time_senior_type==0">
                    {{ scope.row.time_series_range.start_hour}}:00~{{ scope.row.time_series_range.end_hour}}:00
                  </span>
                  <span v-else>
                    高级自定义时间段
                  </span>
                </p>
                <p class="info"><span class="item">投放平台</span><span>{{ scope.row.site_set_name }}</span></p>
                <p class="info"><span class="item">广告出价</span>
                  <span v-if="scope.row.optimization_goal=='OPTIMIZATIONGOAL_CLICK'">CPC {{ scope.row.bid_amount / 100 }}元</span>
                  <span v-else-if="scope.row.optimization_goal=='OPTIMIZATIONGOAL_IMPRESSION'">CPM {{ scope.row.bid_amount / 100 }}元</span>
                  <span v-else>oCPA {{ scope.row.bid_amount / 100 }}元</span>
                </p>
                <div class="info target-flow">
                  <span class="item">定向条件</span>

                  <span v-if="target[scope.row.adgroup_id] === undefined || target[scope.row.adgroup_id].length==0">
                    <ul><li>全定向</li></ul>
                  </span>
                  <span v-else>
                    <ul>
                      <li v-for="item in target[scope.row.adgroup_id]">
                        {{item.name}}：{{item.value}}
                      </li>
                    </ul>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </template>
      </el-table-column>
        <el-table-column fixed="left" type="selection">
        </el-table-column>
        <el-table-column prop="adgroup_name" label="广告组名称" >
          <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.adgroup_name }}</span>
            <i class="el-icon-edit" @click="handleListAdgroupNameChange(scope.row)"></i>
          </template>
        </el-table-column>
        <el-table-column prop="adgroup_id" key="adgroup_id" label="广告组ID" v-if="checkItems('adgroup_id')" >
        </el-table-column>
        <el-table-column prop="campaign_id" key="campaign_id" label="计划ID" v-if="checkItems('campaign_id')" >
        </el-table-column>
        <el-table-column prop="product_type_name" key="product_type_name" label="标的物类型" v-if="checkItems('product_type_name')">
        </el-table-column>
        <el-table-column prop="product_name" key="product_name" label="标的物名称" v-if="checkItems('product_name')">
        </el-table-column>
        <el-table-column prop="product_refs_id" key="product_refs_id" label="标的物ID" v-if="checkItems('product_refs_id')">
        </el-table-column>

        <el-table-column v-for="(item, index) in checkBox[2]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)" sortable='custom'>
        </el-table-column>
        <el-table-column v-for="(item, index) in checkBox[3]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)" sortable='custom'>
        </el-table-column>
        <el-table-column v-for="(item, index) in checkBox[4]['sub']" :key="index" :prop="index" :label="item" v-if="checkItems(index)" sortable='custom'>
        </el-table-column>
        <el-table-column label="出价(元)" key="bid_amount" v-if="checkItems('bid_amount')">
          <template slot-scope="scope">
            <span v-if="scope.row.optimization_goal=='OPTIMIZATIONGOAL_CLICK'">
              <span style="margin-left: 10px">CPC {{ scope.row.bid_amount / 100 }}元</span>
            </span>
            <span v-else-if="scope.row.optimization_goal=='OPTIMIZATIONGOAL_IMPRESSION'">
              <span style="margin-left: 10px">CPM {{ scope.row.bid_amount / 100 }}元</span>
            </span>
            <span v-else>
              <span style="margin-left: 10px">oCPA {{ scope.row.bid_amount / 100 }}元</span>
            </span>
            <i class="el-icon-edit" @click="handleListBidAmountChange(scope.row)"></i>
          </template>
        </el-table-column>
        <el-table-column prop="created_time" label="创建时间" v-if="checkItems('created_time')" sortable>
        </el-table-column>
        <el-table-column prop="last_modified_time" label="修改时间" v-if="checkItems('last_modified_time')" sortable>
        </el-table-column>
        <el-table-column key="system_status" label="状态" v-if="checkItems('status')">
          <template slot-scope="scope">
            <span v-if="scope.row.system_status=='AD_STATUS_DENIED'">
              <span style="margin-left:10px;color:red;">{{ system_status[scope.row.system_status] }}</span>
              <el-tooltip placement="bottom" effect="light">
                <div slot="content" style="width:200px;">
                  <p>{{ scope.row.reject_message }}</p>
                </div>
                <el-button type="text"><i class="el-icon-question"></i></el-button>
              </el-tooltip>
            </span>
            <span v-else>
              <span style="margin-left:10px">{{ system_status[scope.row.system_status] }}</span>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop="configured_status" label="开关" sortable>
          <template slot-scope="scope">
            <el-switch @change="handleListStatusChange(scope.row)" v-model="scope.row.configured_status === 'AD_STATUS_NORMAL' ? true : false" active-color="#13ce66" inactive-color="#999">
            </el-switch>
          </template>
        </el-table-column>
        <!-- <el-table-column prop="" label="预警" sortable>
        </el-table-column> -->
        <el-table-column prop="" label="操作">
          <template slot-scope="scope">
            <el-button @click="handleEditClick(scope.row.adgroup_id)" type="text" size="small">编辑</el-button>
            <el-button @click="handleCopyClick(scope.row.adgroup_id)" type="text" size="small">复制</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="table-pagination" v-if="page_info.total_number>0">
        <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page_info.page" :page-sizes="[20, 50, 100]" :page-size="page_info.page_size" layout="total, sizes, prev, pager, next, jumper" :total="page_info.total_number">
        </el-pagination>
      </div>
    </div>
  </div>

</template>

<script>
import options from '../../util/options.js'
import vTarget from '../../components/vTarget'
export default {
  components: {
    'v-target': vTarget
  },
  created: function() {
    var params = this.$route.params || {}
    if (params.hasOwnProperty('campaign_id')) {
      this.adgroupsParams.campaign_id = params.campaign_id
    }
    if (this.account_id) {
      this.targetApi()
      this.getAdgroups(this.adgroupsParams)
    }
    this.getAdCommongetEnum()
  },
  watch: {
    // 监听 account_id 发生变化
    account_id() {
      this.targetApi()
      this.getAdgroups(this.adgroupsParams)
    }
  },
  computed: {
    account_id() {
      var account = this.$store.getters.get_account_action
      return account.account_id
    },
    regionsMap() {
      var map = {}
      var regions = this.options.region
      regions.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },
    appCategoryMap() {
      var map = {}
      var app_category = this.options.app_category
      app_category.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },
    businessInterestMap() {
      var map = {}
      var business_interest = this.options.business_interest
      business_interest.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },
    audienceMap () {
      var map = {}
      this.audienceList.forEach(element => {
        map[element.id] = element.label
      });
      return map
    },
  },
  data() {
    return {
      target: {
      },
      options: options,
      adsList: {},
      imagesList: {},
      visible: false,
      checkBox: [{
        'name' : '必选项',
        'disabled' : true,
        'sub' : {
          'adgroup_name':'广告组名称',
          'operation':'操作',
          }
      },
      {
        'name' : '属性',
        'disabled' : false,
        'sub' : {
          'product_type_name':'标的物类型',
          'adgroup_id':'广告组ID',
          'campaign_id':'计划ID',
          'product_name':'标的物名称',
          'product_refs_id':'标的物ID',
          'bid_amount':'出价',
          'status':'状态',
          'created_time':'创建时间',
          'last_modified_time':'修改时间'
          }
      },
      {
        'name' : '效果',
        'disabled' : false,
        'sub' : {
          'impression':'曝光量',
          'click':'点击量',
          'ctr':'点击率',
          'cpc':'点击均价(元)',
          'conversion':'安装量(转化量)',
          'clicktraderate':'安装率(转化率)',
          'cpt':'安装(转化)均价(元)',
          'cost':'花费(元)',
          }
      },
      {
        'name' : 'APP转化效果',
        'disabled' : false,
        'sub' : {
          'download':'下载量',
          'downloadrate':'下载率',
          'app_installation':'安装量',
          'cost_per_download':'下载均价',
          'activation':'激活总量',
          'activated_rate':'下载激活率',
          'click_activated_rate':'点击激活率',
          'activated_price':'激活均价(元)',
          'register': 'APP注册量',
          'app_payment_count': '付费行为量',
          'order_placement': '下单量',
          'app_payment_amount': '付费总金额(分)',
          'reservation': '表单预约量',
          }
      },
      {
        'name' : '社交',
        'disabled' : false,
        'sub' : {
          'follow': '关注量',
          'like_or_comment': '点赞量',
          'like_or_comment': '评论量',
          'share': '转发量',
          'image_click': '图片点击量',
          }
      },
      ],
      

      checkList: [['adgroup_name','operation'],['campaign_type_name','product_type_name','product_name','bid_amount','status'],['impression','click','ctr','cpc','cost'],['activation'],[]],
      originCheckList: [['adgroup_name','operation'],['campaign_type_name','product_type_name','product_name','bid_amount','status'],['impression','click','ctr','cpc','cost'],['activation'],[]],
      isIndeterminate: [false,false,false,false,false],
      checkBoxAll: [true,false,false,false,false],
      configured_status_list: {},
      system_status: {},
      ad_system_status: [],
      loading: true,
      adgroupsList: [],
      productTypeList: [
        {
          value: '',
          name: '标的物类型'
        }
      ],
      adgroupsParams: {
        page: 1,
        page_size: 20,
        adgroup_name: '',
        system_status: '',
        product_type: '',
        campaign_id: '',
        adgroup_id: '',
        report: 1,
        start_date: '',
        end_date: '',
        sort_field: '',
        sort_type: ''
      },
      listData: [],
      sumData: [],
      multipleSelection: [],
      audienceList: [],
      excludeAudienceList: [],
      configured_status_command: '',
      page_info: {},
      pickerDate: [new Date(), new Date()],
      value7: '',
      pickerOptions2: {
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去7天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', [start, end])
            }
          },
          {
            text: '过去30天',
            onClick(picker) {
              const end = new Date()
              const start = new Date()
              end.setTime(end.getTime() - 3600 * 1000 * 24)
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
              picker.$emit('pick', [start, end])
            }
          }
        ]
      }
    }
  },
  methods: {
    handleCheckAllChange(val) {
      this.checkList[val] = this.checkBoxAll[val] ? Object.keys(this.checkBox[val]['sub']) : []
    },
    checkBoxCancel(){
      this.checkList = JSON.parse(JSON.stringify(this.originCheckList))
      this.visible = false
    },
    checkBoxSubmit(){
      this.originCheckList = JSON.parse(JSON.stringify(this.checkList))
      this.visible = false
    },
    checkItems(val){
      var arr = this.checkList[1].concat(this.checkList[2]);
      arr = arr.concat(this.checkList[3]);
      arr = arr.concat(this.checkList[4]);
      if(arr.indexOf(val) !== -1){
        return true
      }else{
        return false
      }
    },
    changeSort(val){
      if(val.order && val.column.sortable == 'custom'){
        this.adgroupsParams.sort_field = val.prop
        this.adgroupsParams.sort_type = val.order
        this.getAdgroups(this.adgroupsParams)
      }
    },
    // 批量删除
    handleBatchRemove() {
      var rows = this.multipleSelection,
        that = this
      if (rows && rows.length > 0) {
        var ids = [],
          isConfigured = false
        for (var i in rows) {
          // 判断该广告组是否在开启状态下
          if (rows[i]['configured_status'] == 'AD_STATUS_NORMAL') {
            isConfigured = true
            break
          }
          ids.push(rows[i]['adgroup_id']) // 添加id
        }

        if (!isConfigured) {
          this.$confirm('是否确认批量删除记录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          })
            .then(() => {
              var params = {
                ids: ids
              }
              that.adgroupsBatchDelete(params) // 批量删除请求
            })
            .catch(() => {})
        } else {
          that.$message('广告组关闭状态下才能进行删除操作')
        }
      } else {
        that.$message('请选择需要修改的数据')
      }
    },
    // 批量修改 投放日期 (修改时间)
    handleBatchChangeDateRange() {
      var rows = this.multipleSelection,
        that = this
      if (rows && rows.length > 0) {
        var ids = []
        for (var i in rows) {
          ids.push(rows[i]['adgroup_id'])
        }
        that.$message('开发中')
      } else {
        that.$message('请选择需要修改的数据')
      }
    },
    // 批量修改状态
    submitSystemStatus() {
      var rows = this.multipleSelection
      var that = this
      if (rows && rows.length > 0) {
        var configured_status_command = that.configured_status_command
        if (configured_status_command == '') {
          that.$message('请选择需要修改的状态')
          return false
        }
        var ids = []
        for (var i in rows) {
          ids.push(rows[i]['adgroup_id'])
        }
        var params = {
          ids: ids,
          configured_status: configured_status_command
        }
        that.adgroupsBatchChange(params) // 广告组批量请求
        // rows.forEach(row => {
        //   row.configured_status = that.configured_status_command
        //   that.$refs.multipleTable.toggleRowSelection(row)
        // })
      } else {
        //that.$refs.multipleTable.clearSelection()
        that.$message('请选择需要修改的数据')
      }
    },
    // 选择 状态
    handleCommand(command) {
      this.configured_status_command = command
    },

    // table 选中事件
    handleCurrentTable(val) {
      // console.log(val)
      if(val.loading == true){
        // this.bingParams(this.target.targeting)
        // 查询所属广告
        var that = this
        var params = {
          account_id: this.account_id,
          adgroup_id: val.adgroup_id,
          targeting_id: val.targeting_id,
          start_date: this.adgroupsParams.start_date,
          end_date: this.adgroupsParams.end_date
        }
        that.$axios
          .get('/Admin/Marketing_Adgroups/getAds', {
            params: params
          })
          .then(function(response) {
            val.loading = false
            var respData = response.data
            if(val.targeting !== undefined){
              that.target[val.adgroup_id] = that.bingParams(val.targeting)
            }
            if (respData.code == 0) {
              that.imagesList[val.adgroup_id] = respData.data.adcreative_list
              that.adsList[val.adgroup_id] = respData.data.ad_list
              // console.log(that.imagesList[val.adgroup_id])
              if(val.targeting === undefined){
                that.target[val.adgroup_id] = that.bingParams(respData.data.targeting)
              }
            } else {
              that.$message(respData.msg)
            }
          })
          .catch(function(err) {
            val.loading = false
            that.$message('网络繁忙，请稍后再试！')
          })
      }else{
        val.loading = true
      }
    },
    // table 多选事件
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    // 批量修改出价
    handleBatchChangeBidAmount() {
      var list = this.multipleSelection,
        that = this
      if (list.length > 0) {
        var ids = []
        var isReduce = list.reduce(function(x, y) {
          ids.push(x.adgroup_id)
          if (x.optimization_goal == y.optimization_goal) {
            return y
          } else {
            return false
          }
        })
        if (isReduce) {
          ids.push(isReduce.adgroup_id)
          //console.log(ids)
          var unitName = ''
          if (list[0].optimization_goal == 'OPTIMIZATIONGOAL_CLICK') {
            unitName = '元/点击'
          }else if (list[0].optimization_goal == 'OPTIMIZATIONGOAL_IMPRESSION') {
            unitName = '元/千次曝光'
          } else {
            unitName = '元/激活'
          }
          this.$prompt('请输入出价(' + unitName + ')', '编辑', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            inputPattern: /^([1-9]\d*)+(.\d{1,2})?$|^0\.\d{1,2}$/,
            inputErrorMessage: '输入值不能为空'
          })
            .then(({ value }) => {
              var params = {
                ids: ids,
                bid_amount: value
              }
              that.adgroupsBatchChange(params) // 广告组批量请求
            })
            .catch(() => {})
        } else {
          that.$message('请选择同类型出价批量修改')
        }
      } else {
        that.$message('请选择需要修改的数据')
      }
    },
    // 广告组批量 delete 请求
    adgroupsBatchDelete(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adgroups/batchDelete', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.getAdgroups(that.adgroupsParams) // 刷新广告组
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 广告组批量 updata 请求
    adgroupsBatchChange(params) {
      var that = this
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adgroups/batchChange', {
          params: params
        })
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            that.$message({
              type: 'success',
              message: respData.msg
            })
            setTimeout(function() {
              that.getAdgroups(that.adgroupsParams) // 刷新广告组
            }, 500)
          }else{
            that.$message.error(respData.msg)
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 操作 -- 编辑
    handleEditClick(adgroup_id) {
      this.$router.push('/advertising/adGroup/edit/' + adgroup_id)
    },
    // 操作 -- 复制
    handleCopyClick(adgroup_id) {
      this.$router.push('/advertising/adGroup/copy/' + adgroup_id)
    },
    // 修改 -- 广告组名称
    handleListAdgroupNameChange(row) {
      var oldval = row.bid_amount
      var that = this
      this.$prompt('请输入广告组名称', '编辑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^[^/\\'\&]+$/,
        inputErrorMessage: '请输入有效值'
      })
        .then(({ value }) => {
          that.setAdAdgroupsUpdate(row, 'adgroup_name', value, oldval)
          row.adgroup_name = value
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: '取消输入'
          // })
        })
    },

    //  修改出价
    handleListBidAmountChange(row) {
      var unitName = ''
      if (row.optimization_goal == 'OPTIMIZATIONGOAL_CLICK') {
        unitName = '元/点击'
      }else if (row.optimization_goal == 'OPTIMIZATIONGOAL_IMPRESSION') {
        unitName = '元/千次曝光'
      } else {
        unitName = '元/激活'
      }
      var oldval = row.bid_amount
      var that = this
      this.$prompt('请输入出价(' + unitName + ')', '编辑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^([1-9]\d*)+(.\d{1,2})?$|^0\.\d{1,2}$/,
        inputErrorMessage: '请输入有效值'
      })
        .then(({ value }) => {
          that.setAdAdgroupsUpdate(row, 'bid_amount', value, oldval)
          row.bid_amount = value * 100
        })
        .catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: '取消输入'
          // })
        })
    },
    formatterSystemStatus(row, column) {
      return this.system_status[row.system_status]
    },
    handleListStatusChange(row) {
      var oldval = row.configured_status
      if (row.configured_status === 'AD_STATUS_NORMAL') {
        row.configured_status = 'AD_STATUS_SUSPEND'
      } else {
        row.configured_status = 'AD_STATUS_NORMAL'
      }
      this.setAdAdgroupsUpdate(
        row,
        'configured_status',
        row.configured_status,
        oldval
      )
    },
    setAdAdgroupsUpdate(row, key, value, oldval) {
      var that = this
      var params = new Object()
      params['adgroup_id'] = row.adgroup_id
      params[key] = value
      params.account_id = this.account_id
      this.$axios
        .post('/Admin/Marketing_Adgroups/update', params )
        .then(function(response) {
          var respData = response.data
          if (respData.code != 0) {
            that.$message.error(respData.msg)
            row[key] = oldval
          }
        })
        .catch(function(err) {
          that.$message('网络繁忙，请稍后再试！')
          row[key] = oldval
        })
    },
    // 公共枚举
    getAdCommongetEnum() {
      var that = this
      this.$axios
        .get('/Admin/Marketing_Common/getEnum', {})
        .then(function(response) {
          var respData = response.data
          if (respData.code == 0) {
            var product_type = respData.data['PRODUCT_TYPE']
            for (var key in product_type) {
              var obj = new Object()
              obj.value = key
              obj.name = product_type[key]['name']
              that.productTypeList.push(obj)
            }
            that.configured_status_list = respData.data['AD_STATUS']
            that.system_status = respData.data['AD_SYSTEM_STATUS']
            that.ad_system_status.push({ value: '', label: '所有状态' })
            for (var key in respData.data['AD_SYSTEM_STATUS']) {
              var obj = new Object()
              obj.value = key
              obj.label = respData.data['AD_SYSTEM_STATUS'][key]
              that.ad_system_status.push(obj)
            }
          }
        })
        .catch(function(err) {
          console.log(err)
        })
    },
    // 日期选择
    pickerDateChange() {
      var start_date = new Date(this.pickerDate[0]),
        end_date = new Date(this.pickerDate[1])
      this.adgroupsParams.start_date =
        start_date.getFullYear() +
        '-' +
        (start_date.getMonth() + 1) +
        '-' +
        start_date.getDate()
      this.adgroupsParams.end_date =
        end_date.getFullYear() +
        '-' +
        (end_date.getMonth() + 1) +
        '-' +
        end_date.getDate()
      this.getAdgroups(this.adgroupsParams)
    },
    // 查询
    searchList() {
      this.adgroupsParams.page = 1
      // this.adgroupsParams.page_size = 10
      this.getAdgroups(this.adgroupsParams)
    },
    //  广告组 查询请求
    getAdgroups: function(params) {
      var that = this
      that.loading = true
      params.account_id = this.account_id
      this.$axios
        .get('/Admin/Marketing_Adgroups/get', {
          params: params
        })
        .then(function(response) {
          that.loading = false
          var respData = response.data
          if (respData.code == 0) {
            //console.log('respData.list:', respData.data.list)
            if (respData.data.list || respData.data.page_info) {
              that.listData = respData.data.list
              that.sumData = respData.data.sum
              that.page_info = respData.data.page_info
            }
          } else {
            that.$message(respData.msg)
          }
        })
        .catch(function(err) {
          that.loading = false
          that.$message('网络繁忙，请稍后再试！')
        })
    },
    // 分页
    handleSizeChange(val) {
      this.adgroupsParams.page_size = val
      this.getAdgroups(this.adgroupsParams)
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange(val) {
      this.adgroupsParams.page = val
      this.getAdgroups(this.adgroupsParams)
      //console.log(`当前页: ${val}`)
    },
    //导出execel
    exportExcel() {
      var params = this.adgroupsParams
      window.location.href =
        '/Admin/Marketing_Adgroups/export?page=' +
        params.page +
        '&page_size=' +
        params.page_size +
        '&adgroup_name=' +
        params.adgroup_name +
        '&system_status=' +
        params.system_status +
        '&campaign_name=' +
        params.campaign_type +
        '&adgroup_id=' +
        params.adgroup_id +
        '&start_date=' +
        params.start_date +
        '&end_date=' +
        params.end_date +
        '&account_id=' +
        this.account_id
    },
    // 新增广告组
    newAdGroup() {
      this.$router.push('/advertising/adGroup/new')
    },
    getTargetVal(key, val) {
      var ret = []
      if (this.options.hasOwnProperty(key)) {
        if (val instanceof Array) {
          val.forEach(element => {
            if (this.options[key].hasOwnProperty(element)) {
              ret.push(this.options[key][element])
            }
          })
        } else {
          if (this.options[key].hasOwnProperty(val)) {
            ret.push(this.options[key][val])
          }
        }
      }
      return ret
    },
    
    getRegionsVal(val) {
      var ret = []
      val.forEach(element => {
        if (this.regionsMap.hasOwnProperty(element)) {
          ret.push(this.regionsMap[element])
        }
      })
      return ret
    },
    getBusinessInterestVal(val) {
      var ret = []
      val.forEach(element => {
        if (this.businessInterestMap.hasOwnProperty(element)) {
          ret.push(this.businessInterestMap[element])
        }
      })
      return ret
    },
    getCustomAudienceVal (val) {
      var ret = []
      var map = {}
      var count = 0
      this.audienceList.forEach(element => {
        map[element.id] = element
      })

      val.forEach(id => {
        if (map.hasOwnProperty(id)) {
          ret.push(map[id]['label'])
        }
      })
      return ret
    },
    getExcludeCustomAudienceVal (val) {
      var ret = []
      var map = {}
      var count = 0
      this.excludeAudienceList.forEach(element => {
        map[element.id] = element
      })
      val.forEach(id => {
        if (map.hasOwnProperty(id)) {
          ret.push(map[id]['label'])
        }
      })
      return ret
    },
    getAppCategoryVal(val) {
      var ret = []
      val.forEach(element => {
        if (this.appCategoryMap.hasOwnProperty(element)) {
          ret.push(this.appCategoryMap[element])
        }
      })
      return ret
    },
    targetApi () {
      if (!this.account_id) {
        return
      }
      var params = {
        account_id: this.account_id
      }
      var that = this
      this.$axios
        .get('/Admin/Marketing_CustomAudiences/get', {
          params: params
        }).then(function (response) {
          var respData = response.data
          if (respData.code) {
            that.$message(respData.msg)
          } else {
            that.audienceList = respData.data
            that.excludeAudienceList = respData.data
            that.$nextTick(function () {
              that.bingParams()
            })
          }
        })
        .catch(function (err) {
          that.$message('网络繁忙，请稍后再试！')
          console.log(err)
        })
    },
    bingParams(targeting) {
        // if (this.readonly) {
          var targetTable = []
          var mytargettable = {}
          var ret = []

          for (const key in targeting) {
            var val = targeting[key]
            mytargettable = {
              'type': key,
            }
            if (this.options.targeting_types.hasOwnProperty(key)) {
              mytargettable['name'] = this.options.targeting_types[key]
            }
            switch (key) {
              case 'geo_location':
                ret = []
                if (val.hasOwnProperty('location_types')) {
                  ret.push("(" + this.getTargetVal('location_types', val['location_types']).join() + ")")
                }
                if (val.hasOwnProperty('regions')) {
                  ret.push(this.getRegionsVal(val['regions']).join())
                }
                mytargettable['value'] = ret.join()
                break;
              case 'app_behavior':
                mytargettable['value'] = '距离今天 ' + val['time_window'] + ' 天内，[' + this.getAppCategoryVal(val[
                  'object_id_list']).join() + '] 类APP的 ' + this.getTargetVal('act_id_list', val['act_id_list']).join()
                break;
              case 'business_interest':
                mytargettable['value'] = this.getBusinessInterestVal(val).join()
                break;
              case 'custom_audience':
                mytargettable['value'] = this.getCustomAudienceVal(val).join(',')
                break;
              case 'excluded_custom_audience':
                mytargettable['value'] = this.getExcludeCustomAudienceVal(val).join(',')
                break;
              case 'age': 
              case 'residential_community_price':
              case 'temperature':
                mytargettable['value'] = val[0]
                break;
              case 'app_install_status':
                mytargettable['value'] = this.getTargetVal(key, [val]).join()
                break;
              case 'keyword':
                mytargettable['value'] = val['words'].join()
                break;
              default:
                mytargettable['value'] = this.getTargetVal(key, val).join()
                break;
            }
            targetTable.push(mytargettable)
          }
          return targetTable
          console.log(targetTable)
        // }
      },
      getSummaries(param) {
        var arr = this.checkList[0].concat(this.checkList[1]);
        const { columns, data } = param;
        const sums = [];
        const itemKey = [];
        columns.forEach((column, index) => {
          itemKey[column.property] = index
          if (index === 1) {
            sums[index] = '总计';
            return;
          }
          if(arr.indexOf(column.property) !== -1){
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          if (!values.every(value => isNaN(value))) {
            sums[index] = values.reduce((prev, curr) => {
              const value = Number(curr);
              if (!isNaN(value)) {
                return prev + curr;
              } else {
                return prev;
              }
            }, 0);
          } else {
            sums[index] = '';
          }
        });
        sums[itemKey['ctr']] = this.sumData['ctr']
        sums[itemKey['cpc']] = this.sumData['cpc']
        sums[itemKey['clicktraderate']] = this.sumData['clicktraderate']
        sums[itemKey['cpt']] = this.sumData['cpt']
        sums[itemKey['activated_price']] = this.sumData['activated_price']
        return sums;
      }
  }
}
</script>


<style scoped>
.datepop {
    width: 670px;
    overflow: hidden;
}
.datepop .selectarea {
    float: left;
    height: auto;
    width: 640px;
    padding: 20px 15px 10px 0;
    max-height: 400px;
    overflow: auto;
}
.datepop .selectarea .classify-item .tit {
    border-top: 1px solid #E5E5E5;
    position: relative;
    margin-top: 7px;
    height: 1px;
}
.datepop .selectarea .classify-item .tit strong {
    display: block;
    position: absolute;
    background: #fff;
    padding: 0 5px;
    top: -11px;
    left: 0;
}
.datepop .selectarea .classify-item .checkitem {
  display: table;
  margin: 15px 0 0 40px;
}
.datepop .selectarea .classify-item .checkitem-li {
    float: left;
    width: 135px;
    margin-bottom: 10px;
}
.datepop .selectarea .classify-item .checkitem-li .checked {
    color: #13c4a5;
}
.datepop .selectarea ul{
  -webkit-margin-before: 1em;
    -webkit-margin-after: 1em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
    -webkit-padding-start: 5px;
}
.el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item {
    border: 1px solid #d3dce6;
     background-color: white;
  }
  
  /* .el-carousel__item:nth-child(2n+1) {
     background-color: #d3dce6;
  } */
  .ggao-display {
    display: table-cell;
    vertical-align: middle;
    height: 225px;
    width: 220px;
    border: 1px solid #949494;
    text-align: center;
    word-break: break-all;
    word-wrap: break-word;
    background: #fff;
    position: relative;
}
.ggao-display .show-tuwen {
    max-height: 240px;
    overflow: hidden;
}
p.place {
    padding-bottom: 4px;
    padding-top: 1px;
    font-size: 14px;
    line-height: normal;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    word-wrap: normal;
}
a {
    text-decoration: none;
}
.edit-area {
    float: left;
    width: 244px;
    margin-top: 10px;
}
.info {
    padding-left: 75px;
    position: relative;
    zoom: 1;
    line-height: 25px;
    color: #666;
}
.target-flow {
    overflow:auto;
    max-height: 300px;
}
.info .item {
    width: 70px;
    position: absolute;
    left: 0;
    top: 0;
    text-align: center;
    font-weight: 900;
}
.info ul {
    height: auto;
    overflow: hidden;
}
.info li {
    float: left;
    width: 100%;
}
</style>


